<template>
  <div class="body" :style="{ zoom: zoomwh }">
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      zoomwh: '100%'
    }
  },
  mounted() {
    // 关闭浏览器窗口的时候清空浏览器缓存在localStorage的数据 ,刷新也会清空登陆信息
    // 检查cookie
    this.checkCookie()
    // 更新缩放
    this.getWindowSzie()
  },
  methods: {
    /**
     * 获取cookie
     */
    getCookie() {
      const nameEQ = 'ro_monitor' + '='
      const ca = document.cookie.split(';') // 把cookie分割成组
      for (let i = 0; i < ca.length; i++) {
        let c = ca[i] // 取得字符串
        while (c.charAt(0) === ' ') {
          // 判断一下字符串有没有前导空格
          c = c.substring(1, c.length) // 有的话，从第二位开始取
        }
        if (c.indexOf(nameEQ) === 0) {
          // 如果含有我们要的name
          return unescape(c.substring(nameEQ.length, c.length)) // 解码并截取我们要值
        }
      }
      return false
    },
    /**
     * 检查cookie
     */
    checkCookie() {
      if (!this.getCookie()) {
        console.log('没有ro_monitor的cookie')
        // 清除
        localStorage.clear()
      } else {
        console.log('发现ro_monitor的cookie')
      }
    },
    /**
     * 获取窗口大小
     */
    getWindowSzie() {
      const h = screen.height
      const w = screen.width

      if (h < 1080 && w < 1920) {
        this.zoomwh = '75%'
      }
    }
  }
}
</script>
<style scoped>
@import '../src/assets/fonts/font.css';
.body {
  font-family: 'FiraCode-Retina';
}
</style>
