import { returnNewTestSuite, returnNewTestCase, returnNewTestStep } from '../../scripts/common'
export default {
  state: {
    /**
     * 基于前台保存的testsuite
     */
    testsuite: []
  },
  mutations: {
    /**
     * 添加一个新的suite or case
     * @param {*} state {name,index}
     */
    addNewTestSuite(state, payload) {
      if (payload != null) {
        if (payload.indexX === -1) {
          // 添加一个suite
          const suite = returnNewTestSuite(payload.name)
          state.testsuite.push(suite)
        } else {
          if (payload.indexY === -1) {
            // 添加一个case
            const tcase = returnNewTestCase(payload.name)
            state.testsuite[payload.indexX].cases.push(tcase)
          } else {
            // 添加一个step
            const step = returnNewTestStep(payload.name)
            state.testsuite[payload.indexX].cases[payload.indexY].steps.push(step)
          }
        }
        console.log('TESTSUITE 添加成功')
      }
    },
    /**
     * 删除一个老的suite or case
     * TIPS:和增加是不一样的
     * @param {*} state {name,index}
     */
    removeOldTestSuite(state, payload) {
      if (payload != null) {
        if (payload.indexY === -1) {
          // 删除一个suite
          state.testsuite.splice(payload.indexX, 1)
        } else {
          // 删除一个case
          state.testsuite[payload.indexX].cases.splice(payload.indexY, 1)
        }
        console.log('TESTSUITE 删除成功')
      }
    },
    /**
     * 修改一个suite or case的名字
     * TIPS:和增加是不一样的
     * @param {*} state {name,index}
     */
    changeNameTestSuite(state, payload) {
      if (payload != null) {
        if (payload.indexY === -1) {
          // 删除一个suite
          state.testsuite[payload.indexX].name = payload.name
        } else {
          // 删除一个case
          state.testsuite[payload.indexX].cases[payload.indexY].name = payload.name
        }
        console.log('TESTSUITE 修改成功')
      }
    },
    /**
     *
     * @param {*}} state 状态
     * @param {*} payload testsuite载入记录
     */
    uploadTestSuite(state, payload) {
      if (payload != null) {
        const tempjobj = JSON.parse(payload)
        state.testsuite = tempjobj

        console.log('TESTSUITE 载入成功')
      }
    },

    /**
     * 删除步骤
     * @param {*} state
     * @param {*} payload
     */
    deleteStep(state, payload) {
      state.testsuite.forEach(suiteitem => {
        suiteitem.cases.forEach(caseitem => {
          caseitem.steps.forEach((stepitem, index, arr) => {
            if (stepitem.id === payload.id && index === payload.index) {
              arr.splice(index, 1)
            }
          })
        })
      })
    }
  },
  actions: {}
}
