/**
 * 返回昨日任务执行情况
 * @param {*} datetime 日期(debug下，这个参数基本用不到)
 * TIPS: 借用timeline的展示
 */
const getYestdayTaskInfo = datetime => {
  const data = []
  // 组装数据
  const menenum = makeEemu(MESSAGETYPE_EN)
  for (let index = 0; index < 10; index++) {
    const rnum = Math.ceil(Math.random() * 10)
    const rnum60 = Math.ceil(Math.random() * 60)
    const now = new Date()
    const now60 = new Date(now.setMinutes(now.getMinutes() - rnum60))
    const tnd = dateFormat(now60, 'yyyy-mm-dd HH:MM:ss:l')

    const tmtype = Math.floor(Math.random() * menenum.length) // 随机状态
    // 从job取值
    const singletempjob = {
      taskname: `Task0001_MTStar_US00` + index + `自动化测试`,
      status: menenum[tmtype],
      createdate: tnd,
      completedate: nowdate(),
      usetime: [rnum, rnum60]
    }

    data.push(singletempjob)
  }
  return data
}

/**
 * 获取testsuite
 */
const getTestSuite = () => {
  const tempsuites = []
  // 先一波suite
  for (let index = 0; index < 3; index++) {
    const suite = {
      name: 'suite- ' + index,
      cases: [],
      // 工具使用
      show: false
    }
    tempsuites.push(suite)
  }

  // 再填充cases
  tempsuites.forEach(element => {
    for (let index = 0; index < 3; index++) {
      const tempcase = {
        name: 'case AKA-' + index,
        steps: [],
        // 工具使用
        show: false
      }
      element.cases.push(tempcase)
    }
  })

  // 接着填充step
  tempsuites.forEach(element => {
    element.cases.forEach(item => {
      for (let index = 0; index < 3; index++) {
        const tempstep = {
          id: createGUID(),
          name: '',
          request: {
            method: ''
          }
        }
        tempstep.name = 'step 测试 AB=' + index
        if (index % 2 === 0) {
          // 判定条件余数为0时为偶数
          tempstep.request.method = METHOD.POST
        } else {
          tempstep.request.method = METHOD.GET
        }
        tempstep.request.body = {
          mode: 'formdata',
          formbody: [
            { select: true, key: 'loginName', value: 'admin', type: 'text' },
            { select: true, key: 'loginPwd', value: 'admin', type: 'text' }
          ],
          raw: ''
        }
        tempstep.request.header = []
        tempstep.request.url = {
          raw: 'http://127.0.0.1:8090/mtstar',
          protocol: '',
          host: [],
          port: '',
          path: [],
          query: []
        }
        tempstep.response = {}
        item.steps.push(tempstep)
      }
    })
  })

  return tempsuites
}

